import { Grid, Box, Typography, Button } from '@mui/material';
import { styled, css } from '@mui/material/styles';
import CartItem from './CartItem'

const BoxTotal = styled(Box)(
  ({ theme }) => css`
    background: ${theme.palette.grey['100']};
    border: 1px solid ${theme.palette.grey['400']};
    border-radius: 4px;
  `
);

const CartComp = ({cartItem}) => {
    return (
      <Grid container spacing={2}>
        <Grid item xs={12} md={8}>
          {
            cartItem && cartItem.length > 0 ? cartItem.map(x => {
              return (
                <CartItem item={x} />
              )
            }) : ''
          }
        </Grid>
        <Grid item xs={12} md={4}>
          <BoxTotal textAlign="center" display="flex" alignItems="center" justifyContent="center">
            <Box py={5}>
              <Typography fontWeight={'bold'} color={(theme) => theme.palette.text.primary} sx={{ fontSize: 14 }}>Your cart total</Typography>
              <Typography mt={1} mb={3} fontWeight={'bold'} color={(theme) => theme.palette.info.light} sx={{ fontSize: 28 }}>$24</Typography>
              <Button variant="contained">Checkout</Button>
            </Box>
          </BoxTotal>
        </Grid>
      </Grid>            
    )
}

export default CartComp